<script>
  import ButtonGroup from '../../mixins/button-group';

  export default {
    name: 'r-btn-toggle',

    model: {
      prop: 'inputValue',
      event: 'change'
    },

    mixins: [
      ButtonGroup
    ],

    props: {
      inputValue: {
        required: false
      },
      mandatory: Boolean,
      multiple: Boolean,
      autofit: Boolean,
      buttonWidth: String
    },

    computed: {
      classes () {
        return {
          'r-btn-toggle': true,
          'r-btn-toggle_selected': this.hasValue,
          'r-btn-toggle_autofit': this.autofit && !this.buttonWidth
        };
      },

      hasValue () {
        return (this.multiple && this.inputValue.length) ||
          (!this.multiple && this.inputValue !== null &&
            typeof this.inputValue !== 'undefined');
      }
    },

    watch: {
      inputValue: {
        handler () {
          this.update();
        },
        deep: true
      }
    },

    methods: {
      isSelected (i) {
        const item = this.getValue(i);
        if (!this.multiple) {
          return this.inputValue === item;
        }

        return this.inputValue.includes(item);
      },
      updateValue (i) {
        const item = this.getValue(i);
        if (!this.multiple) {
          if (this.mandatory && this.inputValue === item) return;
          return this.$emit('change', this.inputValue === item ? null : item);
        }

        const items = this.inputValue.slice();

        const index = items.indexOf(item);
        if (index > -1) {
          if (this.mandatory && items.length === 1) return;
          items.length >= 1 && items.splice(index, 1);
        } else {
          items.push(item);
        }

        this.$emit('change', items);
      }
    },

    render (h) {
      return h('div', {
        class: this.classes
      }, this.$slots.default);
    }
  };
</script>

<style lang="scss">
  @import "../../styles/components/_button-toggle";
</style>



