<script>
  // Mixins
  import Delayable from '../../mixins/delayable';
  import Dependent from '../../mixins/dependent';
  import Detachable from '../../mixins/detachable';
  import Menuable from '../../mixins/menuable';
  import Returnable from '../../mixins/returnable';
  import Toggleable from '../../mixins/toggleable';

  // Component level mixins
  import Activator from './mixins/menu-activator';
  import Generators from './mixins/menu-generators';
  import Keyable from './mixins/menu-keyable';
  import Position from './mixins/menu-position';

  // Directives
  import ClickOutside from '../../directives/click-outside';
  import Resize from '../../directives/resize';

  export default {
    name: 'r-menu',

    mixins: [
      Activator,
      Dependent,
      Delayable,
      Detachable,
      Generators,
      Keyable,
      Menuable,
      Position,
      Returnable,
      Toggleable
    ],

    directives: {
      ClickOutside,
      Resize
    },

    data () {
      return {
        defaultOffset: 8,
        maxHeightAutoDefault: '200px',
        startIndex: 3,
        stopIndex: 0,
        hasJustFocused: false,
        resizeTimeout: null
      };
    },

    props: {
      auto: Boolean,
      closeOnClick: {
        type: Boolean,
        default: true
      },
      closeOnContentClick: {
        type: Boolean,
        default: true
      },
      disabled: Boolean,
      fullWidth: Boolean,
      fitToContent: Boolean,
      maxHeight: { default: 'auto' },
      offsetX: Boolean,
      offsetY: Boolean,
      openOnClick: {
        type: Boolean,
        default: true
      },
      openOnHover: Boolean,
      origin: {
        type: String,
        default: 'top left'
      },
      transition: {
        type: [ Boolean, String ],
        default: 'menu-transition'
      }
    },

    computed: {
      calculatedLeft () {
        if (!this.auto) {
          return this.calcLeft();
        }

        return `${this.calcXOverflow(this.calcLeftAuto())}px`;
      },
      calculatedMaxHeight () {
        return this.auto
          ? '200px'
          : isNaN(this.maxHeight)
            ? this.maxHeight
            : `${this.maxHeight}px`;
      },
      calculatedMaxWidth () {
        if (this.fitToContent) {
          const width = (
            this.dimensions.activator.width +
            this.nudgeWidth +
            (this.auto ? 16 : 0)
          );
          return `${width}px`;
        } else {
          return isNaN(this.maxWidth)
            ? this.maxWidth
            : `${this.maxWidth}px`;
        }
      },
      calculatedMinWidth () {
        if (this.minWidth) {
          return isNaN(this.minWidth)
            ? this.minWidth
            : `${this.minWidth}px`;
        }

        const minWidth = (
          this.dimensions.activator.width +
          this.nudgeWidth +
          (this.auto ? 16 : 0)
        );

        if (this.fitToContent) {
          return `${minWidth}px`;
        }

        const calculatedMaxWidth = isNaN(parseInt(this.calculatedMaxWidth))
          ? minWidth
          : parseInt(this.calculatedMaxWidth);

        return `${Math.min(
          calculatedMaxWidth,
          minWidth
        )}px`;
      },
      calculatedTop () {
        if (!this.auto || this.isAttached) {
          return this.calcTop();
        }

        return `${this.calcYOverflow(this.calcTopAuto())}px`;
      },
      styles () {
        return {
          maxHeight: this.calculatedMaxHeight,
          minWidth: this.calculatedMinWidth,
          maxWidth: this.calculatedMaxWidth,
          top: this.calculatedTop,
          left: this.calculatedLeft,
          transformOrigin: this.origin,
          zIndex: this.zIndex || this.activeZIndex
        };
      }
    },

    watch: {
      activator (newActivator, oldActivator) {
        this.removeActivatorEvents(oldActivator);
        this.addActivatorEvents(newActivator);
      },
      isContentActive (val) {
        this.hasJustFocused = val;
      }
    },

    methods: {
      activate () {
        // This exists primarily for r-select
        // helps determine which tiles to activate
        this.getTiles();
        // Update coordinates and dimensions of menu
        // and its activator
        this.updateDimensions();
        // Start the transition
        requestAnimationFrame(this.startTransition);
        // Once transitioning, calculate scroll position
        setTimeout(this.calculateScroll, 50);
      },
      closeConditional () {
        return this.isActive && this.closeOnClick;
      },
      onResize () {
        if (!this.isActive) {
          return;
        }

        // Account for screen resize
        // and orientation change
        // eslint-disable-next-line no-unused-expressions
        this.$refs.content.offsetWidth;
        this.updateDimensions();

        // When resizing to a smaller width
        // content width is evaluated before
        // the new activator width has been
        // set, causing it to not size properly
        // hacky but will revisit in the future
        clearTimeout(this.resizeTimeout);
        this.resizeTimeout = setTimeout(this.updateDimensions, 100);
      }
    },

    render (h) {
      const data = {
        staticClass: 'menu',
        class: {
          'menu_disabled': this.disabled
        },
        style: {
          display: this.fullWidth ? 'block' : 'inline-block'
        },
        directives: [{
          arg: 500,
          name: 'resize',
          value: this.onResize
        }],
        on: {
          keydown: this.changeListIndex
        }
      };

      return h('div', data, [
        this.genActivator(),
        this.genTransition()
      ]);
    }
  };
</script>

<style lang="scss">
  @import "../../styles/components/_menu.scss";
</style>



